import { Navigation } from "@/components/navigation"
import { TransactionItem } from "@/components/transaction-item"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { SearchIcon, FilterIcon } from 'lucide-react'
import { createClient } from '@/lib/supabase/server'
import { redirect } from 'next/navigation'

interface Transaction {
  id: string
  amount: number
  type: "income" | "expense"
  reason: string
  category: string
  date: string
}

// Helper function to filter transactions by date range
const filterTransactionsByDate = (transactions: Transaction[], filter: string) => {
  const now = new Date()
  
  if (filter === "daily") {
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
    return transactions.filter(t => {
      const transactionDate = new Date(t.date)
      const transactionDay = new Date(transactionDate.getFullYear(), transactionDate.getMonth(), transactionDate.getDate())
      return transactionDay.getTime() === today.getTime()
    })
  }
  
  if (filter === "weekly") {
    const startOfWeek = new Date(now)
    startOfWeek.setDate(now.getDate() - now.getDay())
    const endOfWeek = new Date(startOfWeek)
    endOfWeek.setDate(startOfWeek.getDate() + 6)
    
    return transactions.filter(t => {
      const transactionDate = new Date(t.date)
      return transactionDate >= startOfWeek && transactionDate <= endOfWeek
    })
  }
  
  if (filter === "monthly") {
    const startOfMonth = new Date(now.getFullYear(), now.getMonth(), 1)
    const endOfMonth = new Date(now.getFullYear(), now.getMonth() + 1, 0)
    
    return transactions.filter(t => {
      const transactionDate = new Date(t.date)
      return transactionDate >= startOfMonth && transactionDate <= endOfMonth
    })
  }
  
  // "all" filter returns all transactions
  return transactions
}

// Helper function to filter transactions by type
const filterTransactionsByType = (transactions: Transaction[], type: string) => {
  if (type === "income") {
    return transactions.filter(t => t.type === "income")
  }
  
  if (type === "expense") {
    return transactions.filter(t => t.type === "expense")
  }
  
  // "all" type returns all transactions
  return transactions
}

export default async function TransactionsPage() {
  const supabase = await createClient()
  
  // Get user data
  const { data: { user } } = await supabase.auth.getUser()
  
  if (!user) {
    redirect('/login')
  }
  
  // Get transactions
  const { data: transactions, error } = await supabase
    .from('transactions')
    .select('*')
    .eq('user_id', user.id)
    .order('date', { ascending: false })
  
  if (error) {
    console.error('Error fetching transactions:', error)
    // Handle error appropriately
  }
  
  return (
    <div className="flex min-h-screen flex-col md:flex-row">
      <Navigation />
      <main className="flex-1 p-4 pb-20 md:p-8 md:pb-8">
        <div className="mx-auto max-w-4xl space-y-6">
          <h1 className="text-3xl font-bold">Transactions</h1>
          
          <div className="flex flex-col sm:flex-row gap-4">
            <div className="relative flex-1">
              <SearchIcon className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
              <Input
                placeholder="Search transactions..."
                className="pl-10"
                id="transaction-search"
              />
            </div>
            <div className="flex gap-2">
              <Button variant="outline" className="gap-2">
                <FilterIcon className="h-4 w-4" />
                <span>All</span>
              </Button>
            </div>
          </div>
          
          <Tabs defaultValue="all">
            <TabsList className="grid w-full grid-cols-4">
              <TabsTrigger value="all">All</TabsTrigger>
              <TabsTrigger value="daily">Daily</TabsTrigger>
              <TabsTrigger value="weekly">Weekly</TabsTrigger>
              <TabsTrigger value="monthly">Monthly</TabsTrigger>
            </TabsList>
            <TabsContent value="all" className="mt-6 space-y-4">
              {transactions && transactions.length > 0 ? (
                transactions.map((transaction) => (
                  <TransactionItem
                    key={transaction.id}
                    transaction={{
                      id: transaction.id,
                      amount: transaction.amount,
                      type: transaction.type as "income" | "expense",
                      reason: transaction.reason,
                      category: transaction.category,
                      date: transaction.date
                    }}
                  />
                ))
              ) : (
                <div className="text-center py-12 text-muted-foreground">
                  No transactions found
                </div>
              )}
            </TabsContent>
            <TabsContent value="daily" className="mt-6 space-y-4">
              {transactions && transactions.length > 0 ? (
                filterTransactionsByDate(transactions, "daily").map((transaction) => (
                  <TransactionItem
                    key={transaction.id}
                    transaction={{
                      id: transaction.id,
                      amount: transaction.amount,
                      type: transaction.type as "income" | "expense",
                      reason: transaction.reason,
                      category: transaction.category,
                      date: transaction.date
                    }}
                  />
                ))
              ) : (
                <div className="text-center py-12 text-muted-foreground">
                  No transactions found
                </div>
              )}
            </TabsContent>
            <TabsContent value="weekly" className="mt-6 space-y-4">
              {transactions && transactions.length > 0 ? (
                filterTransactionsByDate(transactions, "weekly").map((transaction) => (
                  <TransactionItem
                    key={transaction.id}
                    transaction={{
                      id: transaction.id,
                      amount: transaction.amount,
                      type: transaction.type as "income" | "expense",
                      reason: transaction.reason,
                      category: transaction.category,
                      date: transaction.date
                    }}
                  />
                ))
              ) : (
                <div className="text-center py-12 text-muted-foreground">
                  No transactions found
                </div>
              )}
            </TabsContent>
            <TabsContent value="monthly" className="mt-6 space-y-4">
              {transactions && transactions.length > 0 ? (
                filterTransactionsByDate(transactions, "monthly").map((transaction) => (
                  <TransactionItem
                    key={transaction.id}
                    transaction={{
                      id: transaction.id,
                      amount: transaction.amount,
                      type: transaction.type as "income" | "expense",
                      reason: transaction.reason,
                      category: transaction.category,
                      date: transaction.date
                    }}
                  />
                ))
              ) : (
                <div className="text-center py-12 text-muted-foreground">
                  No transactions found
                </div>
              )}
            </TabsContent>
          </Tabs>
        </div>
      </main>
    </div>
  )
}